  <div class="addRepayment">
    <div class="container">
      <van-cell-group :border="false">
        <van-field
          v-model="form.companyName"
          label="客户"
          type="text"
          input-align="right"
          placeholder="请输入"
          :error="false"
          :border="false"
          :readonly="true"
        />

        <div class="select-list">
          <div class="list">
            <div class="left">
              所属项目
            </div>
            <div class="right">
              <select
                v-model="form.projectName"
                v-placeholder="{color: form.projectName}"
                :disabled="OTP"
              >
                <option
                  v-for="(item,idx) in pgList"
                  :key="idx"
                  :value="item.information.projectName"
                >
                  {{ item.information.projectName }}
                </option>
              </select>
            </div>
            <div
              v-sanjiao="{color: form.projectName}"
              class="saojiao"
            />
          </div>
        </div>
        
        <div class="money-text">
          <van-field
            v-model="form.rentTime"
            style="width:90%;"
