            :error="false"
            :border="false"
            :readonly="!isShow"
          />
          <span class="right-text">月</span>
        </div>

        <div class="select-list">
          <div
            class="list"
            @click="startTimeClick()"
          >
            <div class="left">
              起租日
            </div>
            <div
              class="right"
              :class="{'selectPlaceholder':form.startRentTime== ''}"
            >
              {{ form.startRentTime== '' ? '请选择' : form.startRentTime }}
            </div>
            <div class="timeBox">
              <span
                class="iconfont iconrili"
                style="color:#aebab4"
              />
            </div>
          </div>
        </div>

        <div class="select-list">
          <div
