              租金支付日期
            </div>
            <div
              class="right"
              :class="{'selectPlaceholder':form.rentPayTime== ''}"
            >
              {{ form.rentPayTime== '' ? '请选择' : form.rentPayTime }}
            </div>
            <div class="timeBox">
              <span
                class="iconfont iconrili"
                style="color:#aebab4"
              />
            </div>
          </div>
        </div>
        <div class="money-text">
          <van-field
