            label="本金部分"
            type="number"
            input-align="right"
            placeholder="请输入本金部分 单位:"
            :error="false"
            :border="false"
            :readonly="!isShow"
          />
          <span class="right-text">万元</span>
        </div>
        <div class="money-text">
          <van-field
            v-model="form.interestPart"
            style="width:90%;"
            label="利息部分"
            type="number"
            input-align="right"
            placeholder="请输入利息部分 单位:"
            :error="false"
            :border="false"
            :readonly="!isShow"
          />
          <span class="right-text">万元</span>
        </div>
        <div class="money-text">
          <van-field
            v-model="form.termRent"
            style="width:90%;"
            label="每期租金"
            type="number"
            input-align="right"
            placeholder="请输入每期租金 单位:"
            :error="false"
            :border="false"
            :readonly="!isShow"
          />
          <span class="right-text">万元</span>
        </div>
      </van-cell-group>

      <div class="footer">
        <van-button
          v-if="isShow"
          class="gradual-btn"
          style="width:100%;"
          type="primary"
          @click="addbut"
        >
          确认信息
        </van-button>
      </div>
    </div>

    <!--  时间选择 -->
    <van-popup
      v-model="selectStartTime"
      position="bottom"
      :overlay="true"
    >
      <van-datetime-picker
        v-model="currentTime"
        type="date"
        :min-date="minDate"
        :formatter="formatter"
        @confirm="startconfirm($event)"
        @cancel="startconfirm()"
      />
    </van-popup>

    <!--  时间选择 -->
    <van-popup
      v-model="selectEndTime"
      position="bottom"
      :overlay="true"
    >
      <van-datetime-picker
        v-model="currentTime"
        type="date"        
        :min-date="minDate"
        :formatter="formatter"
        @confirm="endconfirm($event)"
        @cancel="endconfirm()"
      />
    </van-popup>
  </div>
</template>
<script>
// import { api, get, post } from "@/service";
import { mapState } from 'vuex'
import { formatDate, isNull } from '@/utils'
import { fuLiRentBack,getfuLiRentBack } from '@/service/loan.js'
import { projectList } from '@/service/customer.js'
export default {
  name: 'AddRepayment',
  data() {
    return {
      project: -1, // 所属项目
      tenancy: -1, // 所属项目
      selectStartTime: false,
      selectEndTime: false,
      maxDate: new Date(),
      minDate: new Date(1900, 1, 1),
      currentTime: new Date(),
      form:{
        createUserId:'',
        projectId:'',
        startRentTime:'',
        rentTime:'',
        startRentTime:'',
        rentPayTime:'',
        principalPart:'',
        interestPart:'',
        termRent:'',
        lendingId:'',
        rentBackName:'',
        companyName:''
      },
      
      pgList:[],
      isShow :true,
      OTP:false
    }
  },
  computed: {
  
  },
  watch: {},
  mounted() {
    
  },

  created() { 
    if(this.$route.query.Tid){      
      this.getTdata(this.$route.query.Tid)
      this.isShow = false
      this.OTP = true
    }else{
      this.getProjectList()
      this.form.companyName=this.$store.getters.userData.companyName
      this.form.lendingId=this.$route.query.id
      this.form.rentBackName='第'+this.$route.query.ln+'次还款'
      this.form.createUserId=this.$store.getters.userInfo.id
    }
    
  },
  methods: {
    getTdata(id){
      getfuLiRentBack({id:id}).then(res=>{
        // console.log('getfuLiRentBack',res)
        this.form =res.data      
        this.form.projectName = this.form.information.projectName

        this.pgList.push(this.form)
        // console.log('this.form',this.form,this.pgList)  
      })  
    },
    addbut(){
      this.addRPM()
    },
    addRPM(){
      let params = {...this.form}
      this.pgList.map(d=>{
        params.projectId = d.information.projectName == params.projectName ? d.id: params.projectId
      })
      params.projectId = this.$route.query.pid
      fuLiRentBack(params).then(res=>{
        this.$router.go(-1)
      })
    },
    
    getProjectList() {
      const params = {
        menuType: 1,
        size:1000
      }
      projectList(params).then((res) => {
        console.log('projectList', res)
        if(res.total==0)return
        this.pgList = res.data
        if(res.data.length>0){
          if(this.form.projectName){
            return
          }
          this.form.projectName = res.data[0].information.projectName
        }
      })
    },
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } if (type === 'month') {
        return `${value}月`
      }
      return `${value}日`
    },
    startTimeClick() {
      if(this.$route.query.Tid)return
      this.selectStartTime = true
    },
    endTimeClick() {
      if(this.$route.query.Tid)return
      this.selectEndTime = true
    },
    startconfirm(e) {
      // 选开始时间
      if (isNull(e)) {
        this.selectStartTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd', 'special')
        const timeStr = time.split('-')
        this.form.startRentTime = `${timeStr[0]}-${timeStr[1]}-${timeStr[2]}`
        this.selectStartTime = false
      }
    },
    endconfirm(e) {
      // 选开始时间
      if (isNull(e)) {
        this.selectEndTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd', 'special')
        const timeStr = time.split('-')
        this.form.rentPayTime = `${timeStr[0]}-${timeStr[1]}-${timeStr[2]}`
        this.selectEndTime = false
      }
    },
    onRead() { },
  },
}
</script>
